
.news .main .item {
  margin: 10px;
  border-bottom: 1px solid #f00;
}
.news .main .item img {
  height: 150px;
}
.news .main .item .see {
  font-size: 16px;
  color: #008000;
  margin-right: 10px;
}
.news .main .item h3 {
  color: #000;
  font-size: 20px;
  font-weight: bold;
  margin: 10px;
}
.news .main .item p.info {
  line-height: 1.4;
  margin: 10px;
  color: #333;
}


