
.menus {
  color: #999;
  font-size: 16px;
  width: 200px;
  height: 100%;
  background: #202020;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 9999;
  -webkit-transition: all 1s;
  transition: all 1s;
  text-align: center;
}
.menus .open {
  display: none;
  color: #fff;
  height: 30px;
  width: 30px;
  padding: 10px 0 0 8px;
  cursor: pointer;
  position: absolute;
  top: 0;
  right: -39px;
  z-index: 999;
  line-height: 100px;
  cursor: pointer;
  background: rgba(0,0,0,0.2);
}
.menus .open .line {
  height: 1px;
  width: 20px;
  background-color: #fff;
  margin-bottom: 6px;
}
.menus .aside .active {
  color: #fff;
  background: #1a1a1a;
}
.menus .user {
  height: 230px;
  width: 100%;
}
.menus .user img {
  border-radius: 50%;
  width: 150px;
  height: 150px;
  margin: 30px 20px 10px;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.menus .user img:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
}
.menus li {
  height: 45px;
  margin: 15px 0;
}
.menus li a {
  height: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  font-size: 20px;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-left: 40px;
}
.menus li a span {
  margin: 0 15px;
}
.menus li a:hover {
  color: #fff;
}
@media only screen and (max-width: 800px) {
#app {
    padding-left: 0;
}
#app .open {
    display: block;
}
#app .load {
    left: 0;
}
.toggle {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}
}
.icon {
/* 通过设置 font-size 来改变图标大小 */
  width: 25px;
  height: 25px;
/* 图标和文字相邻时，垂直对齐 */
  vertical-align: -0.15em;
/* 通过设置 color 来改变 SVG 的颜色/fill */
  fill: currentColor;
/* path 和 stroke 溢出 viewBox 部分在 IE 下会显示
     normalize.css 中也包含这行 */
  overflow: hidden;
}


